import './App.css';
import $ from 'jquery';
import sound_1 from './fx/808-KICK.WAV';
import sound_2 from './fx/BASSTT0.WAV';
import sound_3 from './fx/CRISPHAT.WAV';
import sound_4 from './fx/SOF-KIC(.WAV';
import sound_5 from './fx/SOFKIC2(.WAV';
import sound_6 from './fx/TITE-KIC.WAV';
import sound_7 from './fx/TITE-SNR.WAV';
import sound_8 from './fx/TITE2SNR.WAV';
import sound_9 from './fx/TITEHAT(.WAV';
import { render } from '@testing-library/react';
import React, {Component} from 'react';


class App extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      soundLabel:'DRUMPAD',
      volume: 1.0
    }
    
    // bind here
    this.playAudio = this.playAudio.bind(this);
    this.getSoundByID = this.getSoundByID.bind(this);
    this.handleKeyPress = this.handleKeyPress.bind(this);
    this.handleClick = this.handleClick.bind(this);
    this.handleVolume = this.handleVolume.bind(this);
  }

//event listener
componentDidMount(){
  document.addEventListener("keydown", this.handleKeyPress);
}



handleKeyPress(e){
  this.playAudio(this.getSoundByID(e.keyCode))
} 

handleClick(e){
  this.playAudio(this.getSoundByID(e.currentTarget.id))
}

handleVolume(vol){
  this.setState({
    volume: vol.target.value
  })
  // if (e.target.value == 0){ // does not work right
  //   this.setState({
  //     soundLabel: 'MUTED' 
  //   })
  // }
}

playAudio = (sound) => {
  if (sound == null){
    return;
  }
  const audio = new Audio(sound)
  audio.volume = parseFloat(this.state.volume)
  audio.play()
}

getSoundByID = (param) => {
  switch(param){
    case 'Q':
    case 81:
    this.setState({
      soundLabel:'808-KICK'
    });
    return sound_1;
    case 'W':
    case 87:
      this.setState({
        soundLabel:'BASS-TT-0'
      });
      return sound_2;
    case 'E':
    case 69:
      this.setState({
        soundLabel:'CRISP-HAT'
      });
      return sound_3;
    case 'A':
    case 65:
      this.setState({
        soundLabel:'SOFT-KICK-A'
      });
      return sound_4;
    case 'S':
    case 83:
      this.setState({
        soundLabel:'SOFT-KICK-B'
      });
      return sound_5;
    case 'D':
    case 68:
      this.setState({
        soundLabel:'TITE-KICK'
      });
      return sound_6;    
    case 'Z':
    case 90:
      this.setState({
        soundLabel:'TITE-SNARE-A'
      });
      return sound_7;
    case 'X':
    case 88:
      this.setState({
        soundLabel:'TITE-SNARE-B'
      });
      return sound_8;
    case 'C':
    case 67:
      this.setState({
        soundLabel:'TITE-HAT'
      });
      return sound_9;       
    default:
      return null;   
  }
}


render() {
  return (
    <div id="drum-machine">
      <div id="display">
        <h1>{this.state.soundLabel}</h1>
        <div className="drumpad" id="Q" onClick={this.handleClick}>Q</div>
        <div className="drumpad" id="W" onClick={this.handleClick}>W</div>
        <div className="drumpad" id="E" onClick={this.handleClick}>E</div><br/>
        <div className="drumpad" id="A" onClick={this.handleClick}>A</div>
        <div className="drumpad" id="S" onClick={this.handleClick}>S</div>
        <div className="drumpad" id="D" onClick={this.handleClick}>D</div><br/>
        <div className="drumpad" id="Z" onClick={this.handleClick}>Z</div>
        <div className="drumpad" id="X" onClick={this.handleClick}>X</div>
        <div className="drumpad" id="C" onClick={this.handleClick}>C</div><br/>
        <input type="range" step='.1' min="0" max="1" onChange={this.handleVolume} />
      </div>

    </div>
  )
}
}
export default App;
